import React, {Component} from "react"

import Cereal from "../dataVizPages/cerealPage"
import Animal from "../dataVizPages/animalcrossing"

class DataCell extends Component { 
	state = {
		boxClass1: "box__row-cell",
		boxClass2: "box__row-cell",
		closeClass: "box__close"
	};

	showContent1 = () => {
		this.setState(state => ({boxClass1: "box__row-cell active", closeClass: "box__close box-close-active"}));
	};

	showContent2 = () => {
		this.setState(state => ({boxClass2: "box__row-cell active", closeClass: "box__close box-close-active"}));
	};

	hideContent = () => {
		this.setState(state => ({
			boxClass1: "box__row-cell",
			boxClass2: "box__row-cell",
			closeClass: "box__close"
		}));
	};
	render() {
		return (
			<div class="box__row">
				<div class={this.state.boxClass1} onClick={this.showContent1}>
					<div class="box__row-cell-info">
						<p class='box__row-cell-info-title'>80 Cereals</p>
					</div>
					<div class="box__row-cell-info--full" >
						<Cereal/>
					</div>
					
				</div>

				<div class={this.state.boxClass2} onClick={this.showContent2}>
					<div class="box__row-cell-info">
						<p class='box__row-cell-info-title'>Animal Crossing Tinder</p>
					</div>
					<div class="box__row-cell-info--full" >
						<Animal/>
					</div>
				</div>

				<div class={this.state.closeClass} onClick={this.hideContent}></div>
			</div>
		);
	}
}

export default DataCell;